export default {
    name: 'JcEditor',
    components: {Editor},
    props: {
        successCode: {
            type: [String, Number, Array],
            default: () => {
                return [200, 0];
            }
        },
        value: {
            type: String,
            default: ''
        },
        // 
        // abc.com/tinymcebaseUrltinymce
        baseUrl: {
            type: String,
            default: './tinymce'
        },
        disabled: {
            type: Boolean,
            default: false
        },
        height: {
            type: [Number, String],
            default: '300'
        },
        width: {
            type: [Number, String],
            default: '100%'
        },
        plugins: {
            type: [String, Array],
            default: () => {
                return 'directionality lists image table wordcount';
            }
        },
        menubar: {
            type: [String, Boolean]
            // 'file edit view insert format tools table'
            , default: false
        },
        toolbar: {
            type: [String, Array],
            default: () => {
                //code undo redo restoredraft | formatpainter removeformat | bold italic underline strikethrough hr forecolor backcolor link | alignleft aligncenter alignright alignjustify | outdent indent indent2em lineheight bullist numlist | blockquote subscript superscript | table image media2 codesample pageembed | styleselect fontselect fontsizeselect | cut copy paste pastetext | axupimgs preview print | help2'
                return [
                    'undo redo | styleselect | bold italic | alignleft aligncenter alignright alignjustify | outdent indent',
                ];
            }
        },
        toolbar_mode: {
            type: String,
            default: 'sliding'
        },
        toolbar_sticky: {
            type: Boolean
            , default: false
        },
        entity_encoding: {
            type: String,
            default: 'named'
        },
        fontsize_formats: {
            type: [String, Array],
            default: "8pt 10pt 12pt 14pt 16pt 18pt 20pt 24pt 36pt"
        },
        contextmenu: {
            type: [String, Array],
            default: 'link image imagetools media2 forecolor backcolor table'
        },
        content_css: {
            type: [String, Array],
            default: () => {
                return [];
            }
        },
        color_map: {
            type: Array,
            default: () => {
                return [
                    '#009688', '',
                    '#5FB878', '',
                    '#1E9FFF', '',
                    '#01AAED', '',
                    '#428bca', '',

                    '#FFB800', '',
                    '#FF5722', '',
                    '#fb6638', 'JC',
                    '#e36c09', '',
                    '#c00000', '',

                    '#000000', '',
                    '#2F4056', '',
                    '#393D49', '',
                    '#c2c2c2', '7',
                    '#d2d2d2', '6',

                    '#dddddd', '5',
                    '#e2e2e2', '4',
                    '#eeeeee', '3',
                    '#f2f2f2', '2',
                    '#F0F0F0', '1',

                    '#ffffff', '',
                ];
            }
        },
        // 
        images_upload_url: {
            type: String
            , default: ''
        },
        images_upload_credentials: {
            type: Boolean
            , default: true
        },
    },
    data() {
        return {
            init: {
                base_url: this.baseUrl,
                cache_suffix: '?v=20210127',

                language_url: `${this.baseUrl}/langs/zh_CN.js`,
                language: 'zh_CN',
                skin_url: `${this.baseUrl}/skins/ui/oxide`,
                height: this.height,
                width: this.width,
                min_height: 300,
                plugins: this.plugins,
                toolbar: this.toolbar,
                toolbar_mode: this.toolbar_mode, // 
                toolbar_sticky: this.toolbar_sticky,
                // , toolbar_sticky: false // ()
                // , my_toolbar_sticky: true // 
                // , my_toolbar_sticky_top: 0 // 

                branding: false, // 
                menubar: this.menubar, // 
                entity_encoding: this.entity_encoding, ///
                fontsize_formats: this.fontsize_formats,
                powerpaste_allow_local_images: true,
                powerpaste_word_import: 'prompt',
                powerpaste_html_import: 'prompt',
                contextmenu: this.contextmenu, // 
                contextmenu_never_use_native: true,
                content_css: this.content_css ? this.content_css : `${this.baseUrl}/skins/content/default/content.css`,
                color_map: this.color_map,
                extended_valid_elements: 'i[class|style],#p[class|style|vj-options],iframe[name|class|src|width|height|style|frameborder=0|title|align|onmouseover|onmouseout|onload|onload],span[style|class],br', //
                elementpath: true, //
                auto_focus: false, // 
                typeahead_urls: true, // 

                images_upload_url: this.images_upload_url,
                images_upload_credentials: this.images_upload_credentials, // cookie
                images_upload_handler: (blobInfo, success, failure) => {
                    if (this.images_upload_url === '') {
                        // base64
                        const img = 'data:image/jpeg;base64,' + blobInfo.base64()
                        success(img)
                    } else {
                        var formData = new FormData();
                        formData.append('upload_type', 'image');
                        formData.append('file', blobInfo.blob(), blobInfo.filename());
                        jiuchet.require({
                            url: this.images_upload_url,
                            dataType: 'json',
                            type: 'post',
                            data: formData,
                            transformData: false,
                            success: function (res) {
                                const codeVerificationArray = isArray(successCode) ? [...successCode] : [...[successCode]]
                                if (codeVerificationArray.includes(res.code)) {
                                    success(res.data['url']);
                                } else {
                                    // console.error('tinymce: ' + res.msg);
                                    failure(res.msg);
                                }

                            },
                            error: function (res) {
                                // console.log(res)
                                failure("" + res.msg);
                            }
                        });
                    }
                },
                video_template_callback: function (data) {
                    return `
                        <span class="vj" vj-options="{}">
                            <video class="video-js vj2021" ${data.poster ? ' poster="' + data.poster + '" ' : ''}controls="controls" preload="none" width="${data.width}" height="${data.height}" src="${data.source}">
                                <source src="${data.source}" ${data.sourcemime ? ' type="' + data.sourcemime + '"' : ''} />
                            </video>
                        </span>`;

                }
            }
            , myValue: this.value
        }
    },
    beforeCreate() {
        this.$emit('Editor', tinymce);
    },
    created() {
        // 
        /*tinymce.PluginManager.add('image2', function (editor, url) {
            // 
            editor.ui.registry.addButton('image2', {
                // text: '<i class="fa fa-home" title="" aria-hidden="true" ></i>',
                // text: '',
                icon: 'image',
                onAction: function () {
                    selector.image(function (res) {
                        if (typeof res !== 'object') return;
                        if (0 != res.length) {
                            var e = "";
                            for (let i in res) e += '<p><img src="' + res[i].url + '" _src="' + res[i].attachment + '" alt="' + res[i].filename + '" style="max-width: 100%"/></p>';
                            editor.insertContent(e);
                        }
                    }, {isedit: 2})

                }
            });

            //  menu/menubar
            /!*editor.ui.registry.addMenuItem('image2', {
                text: '',
                icon: 'image',
                onAction: function () {
                    selector.image(function (res) {
                        if (typeof res !== 'object') return;
                        if (0 != res.length) {
                            var e = "";
                            for (let i in res) e += '<p><img src="' + res[i].url + '" _src="' + res[i].attachment + '" alt="' + res[i].filename + '" style="max-width: 100%"/></p>';
                            editor.insertContent(e);
                        }
                    }, {isedit: 2})
                }
            });*!/

            return {
                getMetadata: function () {
                    return {
                        //
                        name: "",
                        url: "http://www.jiuchet.com"
                    };
                }
            };
        });*/
    },
    mounted() {
        tinymce.init({});
    },
    methods: {
        // => https://github.com/tinymce/tinymce-vue => All available events
        // 
        onClick(e) {
            this.$emit('onClick', e, tinymce)
        },
        // 
        clear() {
            this.myValue = ''
        }
    },
    watch: {
        value(newValue) {
            this.myValue = newValue
        },
        myValue(newValue) {
            this.$emit('input', newValue)
        }
    }
}
</script>
